@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

*{
    font-family: 'Roboto', sans-serif;
    color: rgb(70, 70, 70);
}

ul{
    list-style: none; 
    padding: 0;
}

.checkbox-list{
    text-align: left;
    margin: auto;
    width: max-content ;
}

.checkbox-list input{
    margin-right: 1em;
}

.checkbox-list li:first-child{
    margin-bottom: 1em;
}